import Modal from '@nextcloud/vue/dist/Components/Modal';
import moment from 'moment';
export default {
  name: 'ModalContent',
  components: {
    Modal: Modal
  },
  props: {
    materials: {
      type: Array,
      default: null
    }
  },
  data: function data() {
    return {
      modal: false,
      quantity: null,
      actionName: null,
      mat: []
    };
  },
  methods: {
    showModal: function showModal() {
      this.modal = true;
    },
    closeModal: function closeModal() {
      this.modal = false;
    },
    onAddAction: function onAddAction() {
      var action = {
        id: -1,
        actionName: this.actionName,
        mat: this.mat,
        quantity: this.quantity,
        date: moment().format('YYYY-MM-DD hh:mm')
      };
      this.$emit('clicked', action);
      this.closeModal();
    }
  }
};